@charset "UTF-8";
@import "../../../style/theme/default/var.css";

.tmp-page-error{
  position: relative;
  text-align: center;
  width:100%;
  height:100%;
}
.tmp-page-error.unfind{
  background:url(data:image/jpeg;base64,) no-repeat;
  background-position: center;
}
.error-content{
  position: absolute;
  bottom:30%;
  left: 0;
  width:100%;
}
.error-tips{
  margin-top: 15px;
  font-size: 18px;
  color:var(--theme-font-color-secondary);
}



